<template>
  <div class="player" :class="{ 'player--show': player.show }">
    <div class="position-relative">
      <!-- Progress -->
      <b-progress
        :value="player.playhead"
        :max="current_song.duration"
        class="player__progress"
      />

      <div class="player__content">
        <div class="row align-items-center no-gutters">
          <!-- Artwork -->
          <div class="col-3">
            <div class="player__artwork-container">
              <img
                :src="artwork"
                :alt="`Artwork for song ${current_song.title}`"
                class="player__artwork"
                :class="{ 'player__artwork--spin': player.playing }"
              />
            </div>
          </div>

          <!-- Controls -->
          <div class="col-8 d-flex flex-column align-items-center">
            <div class="player__song-title text-truncate text-center w-75">
              {{ current_song.title }}
            </div>

            <div class="player__controls">
              <button
                @click="prev()"
                class="player__control player__control--previous"
                :disabled="disabled"
              >
                <b-icon
                  icon="skip-backward"
                  aria-label="Previous Song"
                ></b-icon>
              </button>

              <button
                v-if="!player.playing"
                @click="play()"
                class="player__control player__control--play"
                :disabled="disabled"
              >
                <b-icon icon="play-fill" aria-label="Play Song"></b-icon>
              </button>

              <button
                v-if="player.playing"
                @click="pause()"
                class="player__control player__control--pause"
                :disabled="disabled"
              >
                <b-icon icon="pause-fill" aria-label="Pause Song"></b-icon>
              </button>

              <button
                @click="next()"
                class="player__control player__control--next"
                :disabled="disabled"
              >
                <b-icon icon="skip-forward" aria-label="Next Song"></b-icon>
              </button>
            </div>
          </div>
        </div>
      </div>

      <span class="player__time player__time--countdown">
        {{ timeCountdown | timestamp }}
      </span>

      <button @click="close()" class="btn--reset player__close">
        <b-icon icon="x"></b-icon>
      </button>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from "vuex";
import scPlayer from "@/utilities/soundcloud";

export default {
  name: "Player",
  data() {
    return {
      volume: 10,
      prevThreshold: 3
    };
  },
  computed: {
    ...mapState(["current_song", "player"]),
    ...mapGetters([
      "artwork",
      "firstSongIndex",
      "lastSongIndex",
      "nextSongIndex",
      "prevSongIndex",
      "streamUrl",
      "timeCurrent",
      "timeCountdown",
      "timeDuration"
    ]),
    disabled() {
      return this.current_song.id === undefined || this.player.processing;
    }
  },
  mounted() {
    // Update the progress bar
    scPlayer.on("timeupdate", () => {
      this.setPlayhead(scPlayer.audio.currentTime * 1000);
    });

    // When a song sends
    scPlayer.on("ended", () => this.next());
  },
  methods: {
    ...mapMutations([
      "setCurrentSong",
      "setPlayhead",
      "setPlaying",
      "setPlayerVisible",
      "setProcessing"
    ]),

    close() {
      this.stop();
      this.setPlayerVisible(false);
    },
    next() {
      if (this.current_song.playlist_index === this.lastSongIndex)
        this.setCurrentSong({
          index: this.firstSongIndex,
          uri: this.current_song.playlist_uri
        });
      else
        this.setCurrentSong({
          index: this.nextSongIndex,
          uri: this.current_song.playlist_uri
        });

      this.play();
    },
    prev() {
      // If less than threshold && first song, go to last song
      if (
        this.timeCurrent < this.prevThreshold &&
        this.current_song.playlist_index === this.firstSongIndex
      )
        this.setCurrentSong({
          index: this.lastSongIndex,
          uri: this.current_song.playlist_uri
        });
      // If less than threshold, go back a song
      else if (this.timeCurrent < this.prevThreshold)
        this.setCurrentSong({
          index: this.prevSongIndex,
          uri: this.current_song.playlist_uri
        });
      // Otherwise stop song
      else this.stop();

      // Play song after conditional complete
      this.play();
    },
    pause() {
      scPlayer.pause({ streamUrl: this.streamUrl });
      this.setPlaying(false);
    },
    play() {
      this.setProcessing(true);
      scPlayer.play({ streamUrl: this.streamUrl }).then(() => {
        this.setPlaying(true);
        this.setProcessing(false);
      });
    },
    stop() {
      scPlayer.stop({ streamUrl: this.streamUrl });
      this.setPlaying(false);
    },
    mute() {
      if (this.volume > 0) this.volume = 0;
      else this.volume = 1;
      scPlayer.setVolume(this.volume);
    },
    setVolume(value) {
      const setVolume = value !== undefined ? value : this.volume;
      scPlayer.setVolume(setVolume / 10);
    }
  }
};
</script>
