import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    current_song: {
      duration: 0,
      id: null
    },
    player: {
      playhead: 0,
      playing: false,
      processing: false,
      show: false,
      volume: 10
    },
    playlist_current: {
      index: -1,
      playlist: {},
      tracks: [],
      uri: ""
    },
    playlists: {}
  },
  getters: {
    artwork(state) {
      return state.current_song.artwork_url !== undefined
        ? state.current_song.artwork_url
        : "https://via.placeholder.com/100";
    },
    firstSongIndex() {
      return 0;
    },
    lastSongIndex(state) {
      if (state.playlist_current.index === -1) return -1;
      return state.playlist_current.playlist.track_count - 1;
    },
    nextSongIndex(state) {
      if (state.playlist_current.index === -1) return -1;
      return state.current_song.playlist_index + 1;
    },
    prevSongIndex(state) {
      if (state.playlist_current.index === -1) return -1;
      return state.current_song.playlist_index - 1;
    },
    streamUrl(state) {
      if (state.current_song.id === null) return "";
      return `https://api.soundcloud.com/tracks/${state.current_song.id}/stream`;
    },
    timeCurrent(state) {
      return state.player.playhead / 1000;
    },
    timeCountdown(state, getters) {
      return getters.timeDuration - getters.timeCurrent;
    },
    timeDuration(state) {
      return state.current_song.duration / 1000;
    }
  },
  mutations: {
    // Payload: { index, uri }
    setCurrentSong(state, payload) {
      // Set Current Playlist
      state.playlist_current = {
        index: payload.index,
        playlist: state.playlists[payload.uri],
        tracks: state.playlists[payload.uri].tracks,
        uri: payload.uri
      };

      // Set Current Song
      state.current_song = Object.assign(
        state.playlists[payload.uri].tracks[payload.index],
        {
          playlist_uri: payload.uri,
          playlist_index: payload.index
        }
      );

      // Set Player to Playing
      state.player.playing = true;
    },
    setPlayerVisible(state, boolean) {
      state.player.show = boolean;
    },
    setPlayhead(state, time) {
      state.player.playhead = time;
    },
    setPlaying(state, status) {
      state.player.playing = status;
    },
    setPlaylist(state, payload) {
      Vue.set(state.playlists, payload.uri, payload.playlist);
    },
    setProcessing(state, boolean) {
      state.player.processing = boolean;
    }
  },
  actions: {}
});
