import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: () => import("@/views/Home.vue")
  },
  {
    path: "*",
    name: "Page Not Found",
    component: () => import("@/views/PageNotFound.vue")
  },
  {
    path: "/mixing",
    name: "Mixing Submission Form",
    component: () => import("@/views/Mixing.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  routes
});

export default router;
